import React from 'react'
import BannerOliv from '../../components/BannerOliv'
import { PrimaryButton } from 'Shared/components/Button'
import caseStudiesImage from 'Shared/assets/images/illustrations/case-studies.png'
import sephoraLogo from 'Shared/assets/images/logos/sephora-logo-about-us.svg'
import samsungLogo from 'Shared/assets/images/logos/samsung-logo-about-us.svg'
import boseLogo from 'Shared/assets/images/logos/bose-logo-about-us.svg'
import canonLogo from 'Shared/assets/images/logos/canon-logo-about-us.svg'
import careemLogo from 'Shared/assets/images/logos/careem.svg'
import johnsonsLogo from 'Shared/assets/images/logos/johnsons-logo-png-transparent.svg'
import bgImage from 'Shared/assets/images/case-studies/cta.jpg'
import StoriesCardSection from './StoriesCardSection/StoriesCardSection'
import WhyPeopleSection from './WhyPeopleSection/WhyPeopleSection'
import CaseStudyCTA from './CaseStudyCTA/CaseStudyCTA'

const bannerLogos = [
  {
    id: 1,
    icon: sephoraLogo
  },
  {
    id: 2,
    icon: samsungLogo
  },
  {
    id: 3,
    icon: boseLogo
  },
  {
    id: 4,
    icon: canonLogo
  },
  {
    id: 5,
    icon: careemLogo
  },
  {
    id: 6,
    icon: johnsonsLogo
  }
]

const index = () => {
  return (
    <div>
      <BannerOliv
        bgColor={'#EBEDFF'}
        image={caseStudiesImage}
        title='Lorem ipsum dolor sit amet consectuer'
        description='Lorem ipsum dolor sit amet consectetur. Nunc nibh proin senectus donec tincidunt imperdiet augue. Eu scelerisque id.'
        bannerLogos={bannerLogos}
      />
      <StoriesCardSection />
      <WhyPeopleSection />
      <CaseStudyCTA
        title={`Getting started is easy.<br />And completely free, forever.`}
        bgImage={bgImage}
        buttonsOne={
          {
            id: 1,
            name: 'Create Profile',
            link: 'demo2'
          }
        }
      />
    </div>
  )
}

export default index



